.movieRow{
    margin-bottom: 30px;
}

.movieRow h2{
    margin-left: 30px
}

.movieRow--listArea{
    overflow-x: hidden;
    padding-left: 30px;
} 

.movieRow--list{
    transition: all ease 0.5s;
}

.movieRow--item{
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.movieRow--item img{
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.2s;
}

.movieRow--item img:hover{
    transform: scale(1);
}


.left, .right{
    position: absolute;
    height: 225px;
    width: 40px;
    background: rgba(0,0,0,0.6);
    z-index: 9;
    cursor: pointer;
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    transition: all ease 0.5s;
}

.left{
    left: 0;
}

.right{
    right: 0;
}

.movieRow:hover .left, .movieRow:hover .right{
    opacity: 1;
}

@media screen and (max-width: 760px){
    .left, .right{
        opacity: 1;
    }
}