.featured{
    height: 100vh;
    background-size: cover;
    background-position: center;
}

.featured--vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.featured--name{
    font-size: 60px;
    font-weight: bold;
}

.featured--info{
    font-size: 18px;
    font-weight: bold;
    display: flex;
    margin-top: 15px;
    gap: 15px;
}

.featured--points{
    color: #46d369;
}

.featured--description{
    margin-top: 15px;
    font-size: 20px;
    max-width: 40%;
    max-height: 100px;
    overflow-y: hidden;
    color: #999;
}

.featured--buttons{
    margin-top: 25px;
}

.featured--watch, .featured--list{
    font-size: 18px;
    font-weight: bold;
    border-radius: 5px;
    text-decoration: none;
    list-style-type: none;
    margin-right: 10px;
    padding: 12px 25px;
    opacity: 1;
    transition: all ease 0.2s;
}

.featured--watch:hover, .featured--list:hover{
    opacity: 0.7;
}

.featured--watch{
    background: white;
    color: black;
}

.featured--list{
    background: #333;
    color: white;
}

.featured--genres{
    margin-top: 25px;
    font-size: 18px;
    color: #999;
}

@media screen and (max-width: 760px){
    .featured{
        height: 90vh;
    }

    .featured--name{
        font-size: 40px;
    }

    .featured--info{
        font-size: 16px;
    }

    .featured--description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }

    .featured--watch, .featured--list{
        font-size: 16px;
    }

    .featured--genres{
        font-size: 14px;
    }
}
